import { useState } from "react";
import style from "../../FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import FormInput from "../../../Ui/Input/FormInput";
import { useSubjectOneExamLearnerExport } from "../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import { pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";

const ExamLearnerExport = ({ onClose, examId, subjectId }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    criteria: "",
    learner_type: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [financeOtherFeeMiscellaneous] = useSubjectOneExamLearnerExport();

  const onSelectType = (val) => {
    setState((prev) => ({
      ...prev,
      learner_type: val,
    }));
  };
  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: +e.target.value,
    }));
  };
  const onExportLearner = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && examId) {
      setDisabled((pre) => !pre);
      financeOtherFeeMiscellaneous({
        eid: examId,
        bodyParameter: {
          ...state,
          sid: subjectId,
          learner_type:
            state.learner_type === "Slow Learner"
              ? "SLOW"
              : state.learner_type === "Advanced Learner"
              ? "ADVANCED"
              : "",
        },
      })
        .then((res) => {
          if (res?.data?.excel_key) {
            setTimeout(() => {
              setDisabled((pre) => !pre);
              const newWindow = window.open(
                `${pdfCdnUrl}/${res?.data?.excel_key}`,
                "_blank",
                "noopener,noreferrer"
              );
              if (newWindow) newWindow.opener = null;
              onClose();
            }, 2500);
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("slow_adavnced_learner")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <CustomSelect
              selectLabel={t("learner_type")}
              selectedValue={t("please_select_learner_type")}
              options={["Slow Learner", "Advanced Learner"]}
              onClick={onSelectType}
              defalutValue={state.learner_type}
              errorShow={errorField["learner_type"]}
            />
            <FormInput
              label={`${t("criteria")}`}
              placeholder={t("please_enter_criteria")}
              name={"criteria"}
              value={state.criteria}
              type={"tel"}
              onChange={onInputChange}
            />

            <CreateButton label="export" onAction={onExportLearner} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default ExamLearnerExport;
