import { t } from "i18next";
import { useEffect, useState } from "react";
import {
  useSubjectMarkExcelExport,
  useSubjectMarkExcelImport,
  useSubjectOneExamMarks,
} from "../../../../../hooks/member_tab/subject-api";
import QLoader from "../../../../../Loader/QLoader";
import { imageShowUrl } from "../../../../../services/BaseUrl";
import style from "./Markdetails.module.css";
import { Notfication } from "../../../../../validation/Snackbar";
import CreateButton from "../../../../../optimized/Ui/Button/CreateButton";
import UploadExcelButton from "../../../../../optimized/Ui/Button/UploadExcelButton";
import { pdfCdnUrl } from "../../../../../optimized/Services/UrlConfig/BaseUrl";
import { useFileUpload } from "../../../../../hooks/authentication-api";
import { useSubjectOneExamStudentObtainList } from "../../../../../optimized/Hooks/ApiHooks/SubjectManager/subject-manager-api";
import Loading from "../../../../../optimized/Loader/Loading";
import ExamLearnerExport from "../../../../../optimized/UserStaffMember/SubjectTeacher/SubjectExam/ExamLearnerExport";

function MarkDetails({ examView, sid, isDepartment, cid }) {
  const [disabled, setDisabled] = useState(false);
  const [notificationState, setNotificationState] = useState({
    msg: "",
    run: false,
    isSuccess: false,
  });
  const [state, setState] = useState(null);
  const [step, setStep] = useState("");
  const {
    subjectOneExamStudentObtainList,
    subjectOneExamStudentObtainListLoading,
    subjectOneExamStudentObtainListRefetch,
  } = useSubjectOneExamStudentObtainList({
    data: { sid: sid, examId: examView?._id },
    skip: sid ? !examView?._id : !sid,
  });

  const [subjectOneExamMarks] = useSubjectOneExamMarks();

  const [fileUpload] = useFileUpload();
  const [subjectMarkExcelExport] = useSubjectMarkExcelExport();
  const [subjectMarkExcelImport] = useSubjectMarkExcelImport();

  useEffect(() => {
    if (subjectOneExamStudentObtainList?.student_marks) {
      let obj = {};
      for (let dt in subjectOneExamStudentObtainList?.student_marks) {
        obj[dt] = {
          obtain_marks:
            subjectOneExamStudentObtainList?.student_marks?.[dt]?.obtain_marks,
        };
      }
      setState(obj);
    }
  }, [subjectOneExamStudentObtainList?.student_marks]);

  const onInputChange = (e) => {
    if (isDepartment) {
    } else {
      if (+e.target.value < 0) {
        setNotificationState({
          msg: t("obtain_marks_validation_2"),
          run: true,
        });
      } else if (+e.target.value <= +examView?.totalMarks) {
      } else {
        setNotificationState({
          msg: `${t("obtain_marks_validation_1")} ${examView?.totalMarks}`,
          run: true,
        });
      }
      let obj = { ...state };
      if (obj[e.target.name]) {
        obj[e.target.name]["obtain_marks"] = +e.target.value;
      } else {
        obj[e.target.name] = {
          obtain_marks: +e.target.value,
        };
      }
      setState(obj);
    }
  };

  const onSubmitMarks = () => {
    if (isDepartment) {
    } else {
      let flag = true;
      for (let dft in state) {
        let mark = state[dft];
        if (mark?.obtain_marks < 0) {
          setNotificationState({
            msg: t("obtain_marks_validation_2"),
            run: true,
          });
          flag = false;
          break;
        } else if (mark?.obtain_marks > +examView?.totalMarks) {
          setNotificationState({
            msg: `${t("obtain_marks_validation_1")} ${examView?.totalMarks}`,
            run: true,
          });
          flag = false;
          break;
        } else {
        }
      }
      if (flag) {
        setDisabled((pre) => !pre);

        let arr = [];

        for (let odf of subjectOneExamStudentObtainList?.students) {
          arr.push({
            student: odf?._id,
            obtain_marks: state ? state[`${odf?._id}`]?.obtain_marks ?? 0 : 0,
          });
        }
        subjectOneExamMarks({
          sid: sid,
          marksList: {
            examId: examView?._id,
            marks: arr,
          },
        })
          .then(() => {
            // subjectOneExamStudentObtainListRefetch();
            setDisabled((pre) => !pre);
            setNotificationState({
              msg: t("obtain_marks_validation_3"),
              run: true,
              isSuccess: true,
            });
          })
          .catch({});
      }
    }
  };

  const onExcelFileUpload = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey && sid && examView?._id && cid) {
            subjectMarkExcelImport({
              sid: sid,
              bodyParameter: {
                examId: examView?._id,
                cid: cid,
                excel_key: res.data?.imageKey,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  const onExport = () => {
    if (sid && cid && examView?._id) {
      setDisabled((pre) => !pre);
      subjectMarkExcelExport({
        sid: sid,
        bodyParameter: {
          eid: examView?._id,
          cid: cid,
        },
      })
        .then((res) => {
          if (res?.data?.excel_key) {
            setDisabled((pre) => !pre);
            const newWindow = window.open(
              `${pdfCdnUrl}/${res?.data?.excel_key}`,
              "_blank",
              "noopener,noreferrer"
            );
            if (newWindow) newWindow.opener = null;
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  const onStepClose = () => {
    setStep("");
  };

  const onLearner = () => {
    setStep(1);
  };
  // console.info("dsfgdgd", state);
  return (
    <>
      <div className={style.markDetails}>
        <div className={style.markDetailsHeader}>
          <h6>
            <span className={style.span}>{t("exam")} : </span>{" "}
            {examView?.examName}
          </h6>
          <div className={style.md_container}>
            {!isDepartment ? (
              <>
                <UploadExcelButton
                  onExcelFileUpload={onExcelFileUpload}
                  label={t("import_marks_excel")}
                  customStyleLabel={{
                    fontSize: "12px",
                  }}
                />
                <CreateButton
                  label="export"
                  customStyle={{
                    paddingBlock: "0.2rem",
                    margin: "0",
                  }}
                  onAction={onExport}
                />
                <CreateButton
                  label="learner_export"
                  customStyle={{
                    paddingBlock: "0.2rem",
                    margin: "0",
                  }}
                  onAction={onLearner}
                />
                <CreateButton
                  label="save"
                  customStyle={{
                    paddingBlock: "0.2rem",
                    margin: "0",
                  }}
                  onAction={onSubmitMarks}
                />
              </>
            ) : null}
          </div>
        </div>
        <div className={style.markDetailsHeader2}>
          <h6>
            <span className={style.span2}> {t("Maximum Marks")} : </span>{" "}
            {examView?.totalMarks}
          </h6>
          {/* {examView?.examType === "Final" ? (
          ""
        ) : (
          <h6>
            <span className={style.span2}> {t("weightage")} : </span>{" "}
            {examView?.examWeight}%
          </h6>
        )} */}
        </div>

        <div className={style.table}>
          <table>
            <thead className={style.thead}>
              <tr className={style.tr}>
                <th className={`${style.th} ${style.rroll}`}>{t("roll_no")}</th>
                <th
                  className={`${style.th} ${style.rname}`}
                  style={{
                    width: "50%",
                  }}
                >
                  {t("name")}
                </th>
                <th
                  className={`${style.th}`}
                  style={{
                    width: "20%",
                    textAlign: "center",
                  }}
                >
                  {t("obtain_marks")}
                </th>
                <th
                  className={`${style.th}`}
                  style={{
                    width: "10%",
                    textAlign: "center",
                  }}
                >
                  {t("attendance")}
                </th>
              </tr>
            </thead>
            <tbody className={style.tbody}>
              {subjectOneExamStudentObtainList?.students?.map((stu) => (
                <tr className={style.tr} key={stu?._id}>
                  <td className={`${style.rroll} ${style.rrolld} ${style.td}`}>
                    {stu?.studentROLLNO !== "" ? (
                      <h6> {stu?.studentROLLNO}.</h6>
                    ) : (
                      <h6></h6>
                    )}
                  </td>
                  <td className={style.td}>
                    <div className={style.student}>
                      <img
                        src={
                          stu?.studentProfilePhoto
                            ? `${imageShowUrl}/${stu?.studentProfilePhoto}`
                            : "/images/member_tab/class/default_avatar.svg"
                        }
                        alt=""
                      />
                      <h6>
                        {stu?.studentFirstName}{" "}
                        {stu?.studentMiddleName ? stu?.studentMiddleName : " "}{" "}
                        {stu?.studentLastName}
                      </h6>
                    </div>
                  </td>
                  <td
                    className={`${style.td} ${style.tdinputcontaier}`}
                    style={{
                      width: "20%",
                      textAlign: "center",
                    }}
                  >
                    <div className={style.tdinput}>
                      <input
                        type="tel"
                        name={stu?._id}
                        defaultValue={
                          state
                            ? state[`${stu?._id}`]?.obtain_marks === 0
                              ? ""
                              : state[`${stu?._id}`]?.obtain_marks
                            : ""
                        }
                        placeholder="0"
                        onChange={onInputChange}
                      />
                    </div>
                  </td>
                  <td
                    className={style.td}
                    style={{
                      width: "10%",
                      textAlign: "center",
                    }}
                  >
                    {stu.present === null ? "_" : stu.present ? "P" : "A"}
                    {/* {(
                    (stu.obtainMarks * examView?.examWeight) /
                    examView?.totalMarks
                  ).toFixed(2)} */}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          {subjectOneExamStudentObtainListLoading && <Loading />}
        </div>
      </div>
      {disabled && <QLoader />}
      <Notfication
        msg={notificationState.msg}
        run={notificationState.run}
        setRun={() => setNotificationState({ msg: "", run: false })}
        postiton="bottom"
        type={notificationState.isSuccess ? "success" : "error"}
      />

      {step === 1 ? (
        <ExamLearnerExport
          onClose={onStepClose}
          examId={examView?._id}
          subjectId={sid}
        />
      ) : null}
    </>
  );
}

export default MarkDetails;
